<template>
    <div id="hot-preview">
        <div style="background-color:white">
            <HotTable ref="hotTable" :root="root" :settings="hotSettings"></HotTable>
        </div>
        <div>
            <el-button @click="getTableData()" size="medium" style="margin-top: 40px;background: #67c23a;color: #fff;"></el-button>
        </div>
        <div>
            </br>
                1.</br>
                2.</br>
                3.</br>
                4.</br>
                5.</br>
                6.</br>
                7.'''''''''',''''0</br>
                8.
        </div>
    </div>
</template>
<script>
import axios from 'axios'
import HotTable from '@handsontable/vue'
import values from '../../utils/values'
export default {
  data: function () {
    return {
      root: 'test-hot',
      hotSettings: {}
    }
  },
  components: {
    HotTable
  },
  mounted: function () {
    let sessionUser = JSON.parse(sessionStorage.getItem(values.storage.user)) || {}
    const _this = this
    // 
    axios.get('/api/pc/customerDataPc/getTeamAndUser/' + sessionUser.openid).then((resp) => {
      console.log(resp)
      // resp.agents 
      // resp.teams 
      let teamArray = []
      let userArray = []
      for (let i = 0; i < resp.teams.length; i++) {
        teamArray.push(resp.teams[i].teamName)
        userArray[i] = []
      }
      for (let i = 0; i < resp.agents.length; i++) {
        for (let j = 0; j < teamArray.length; j++) {
          for (let k = 0; k < resp.agents[i].teams.length; k++) {
            if (resp.agents[i].teams[k].teamName === teamArray[j]) {
              userArray[j].push(resp.agents[i].name)
            }
          }
        }
      }
      console.log(userArray)
      const tableObj = this.$refs.hotTable.table
      this.hotSettings = {
        data: [
          ['', '', '', 13815165465, 'xxx', 285, 10, '', 20000, 10000, '2018-05-02', '2018-05-03', '2018-05-04', '2018-05-05', '2018-05-06']
        ],
        colHeaders: ['', '', '', '', '', 'W', '', '', '', '', '', '', '', '', ''],
        columns: [
          {
            // 
            // type: 'dropdown',
            // 
            // source: teamArray,
            allowEmpty: false
          },
          {
            // 
            // type: 'dropdown',
            // source: [''],
            allowEmpty: false
          },
          {
            // 
            type: 'text',
            allowEmpty: false,
            validator: _this.emptyValidator
          },
          {
            // 
            type: 'numeric'
          },
          {
            // 
            type: 'text'
          },
          {
            // W
            type: 'numeric'
          },
          {
            // 
            type: 'numeric'
          },
          {
            // 
            type: 'text'
          },
          {
            // 
            type: 'numeric'
          },
          {
            // 
            type: 'numeric'
          },
          {
            // 
            type: 'date',
            dateFormat: 'YYYY-MM-DD'
          },
          {
            // 
            type: 'date',
            dateFormat: 'YYYY-MM-DD'
          },
          {
            // 
            type: 'date',
            dateFormat: 'YYYY-MM-DD'
          },
          {
            // 
            type: 'date',
            dateFormat: 'YYYY-MM-DD'
          },
          {
            // 
            type: 'date',
            dateFormat: 'YYYY-MM-DD'
          }
        ],
        contextMenu: {
          items: {
            'row_above': {
              name: ''
            },
            'row_below': {
              name: ''
            },
            'remove_row': {
              name: ''
            },
            'undo': {
              name: ''
            },
            'redo': {
              name: ''
            },
            'copy': {
              name: ''
            }
          }
        },
        rowHeaders: true,
        autoRowSize: true,
        minRows: 1,
        // minSpareRows: 1,
        fillHandle: {
          autoInsertRow: true
        },
        cells: function (row, column) {
          var cellMeta = {}
          if (column === 0) {
            cellMeta.type = 'dropdown'
            cellMeta.source = teamArray
          } else if (column === 1) {
            cellMeta.type = 'dropdown'
            let index = teamArray.indexOf(tableObj.getDataAtCell(row, 0))
            if (index >= 0) {
              cellMeta.source = userArray[index]
            } else {
              cellMeta.source = []
            }
          } else if (column === 5) {
            if (tableObj.getDataAtCell(row, 5) === 0) {
              cellMeta.valid = false
            }
          } else if (column === 6) {
            if (tableObj.getDataAtCell(row, 6) === 0) {
              cellMeta.valid = false
            }
          } else if (column === 9) {
            // 
            if (tableObj.getDataAtCell(row, 8) !== '' && tableObj.getDataAtCell(row, 9) !== '') {
              if (tableObj.getDataAtCell(row, 9) > tableObj.getDataAtCell(row, 8)) {
                cellMeta.valid = false
              }
            }
          }
          return cellMeta
        }

      }

      this.$refs.hotTable.table.addHook('afterRender', function () {
        console.log('afterRender')
        _this.$refs.hotTable.table.validateCells()
      })
    }, (response) => {
      this.$message.error(response.message ? response.message : '')
    })
  },
  methods: {
    // 
    getTableData: function () {
      const _this = this
      this.$refs.hotTable.table.validateCells(function (callback) {
        if (callback === false) {
          confirm('')
          return false
        } else {
          let r = confirm('')
          if (r === true) {
            const tableData = _this.$refs.hotTable.table.getData()
            console.log(tableData)
            let sessionUser = JSON.parse(sessionStorage.getItem(values.storage.user)) || {}
            // json
            return axios.post('/api/pc/customerDataPc/importExcelData/' + sessionUser.openid, tableData).then(response => {
              if (response === 'import success') {
                confirm('')
              } else {
                confirm('')
              }
            })
          }
        }
      })
      // console.log(this.$refs.hotTable.table.validateCells())
    },
    // 
    trim: function (str) {
      return str.replace(/(^\s*)|(\s*$)/g, '')
    },
    // Empty validator
    emptyValidator: function (value, callback) {
      if (!value || value.length === 0) {
        callback(false)
      } else {
        callback(true)
      }
    },
    // excel,
    uploadExcel (event) {
      const files = event.target.files
      console.log(files)
      // acceptFile
      let acceptFile = true
      // const BreakException = {}
      let file
      let fileArray = []
      // try {
      for (var i = 0; i < files.length; i++) {
        file = files[i]
        fileArray.push(file)
        // csv
        // https://www.sitepoint.com/mime-types-complete-list/
        // https://blogs.msdn.microsoft.com/vsofficedeveloper/2008/05/08/office-2007-file-format-mime-types-for-http-content-streaming-2/
        // if (file.type !== 'text/csv' && file.type !== 'application/vnd.ms-excel' && file.type !== 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet') {
        //   acceptFile = false
        //   // 
        //   throw BreakException
        // }
      }
      // } catch (e) {
      //   if (e !== BreakException) throw e
      // }
      if (acceptFile === false) {
        alert('csvexcel')
      } else {
        // 
        const uploaders = fileArray.map(file => {
          const formData = new FormData()
          formData.append('file', file)
          return axios.post('/backend/uploadExcel', formData, {headers: {'Content-Type': 'multipart/form-data'}}).then(response => {
            console.log(response.data)
          })
        })
        axios.all(uploaders).then(() => {
          alert('upload success')
        })
      }
    }
  }
}
</script>
<style src="../../../node_modules/handsontable/dist/handsontable.full.css"></style>
<style>
#test-hot {
  width: 100%;
  height: 600px;
  overflow: hidden;
}
.handsontable td.htInvalid {
  background-color: yellow !important;
}
</style>



